* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px
}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    /* CENTRAR OBJETOS */
    border: 1px solid grey;
    /*  */
    border-radius: 6px;
    /* BORDE REDONDEADO */
    margin-bottom: 10px;
    /*  */
    margin-right: 10px;
    /*  */
    overflow: hidden;
    /* PARA QUE LOS BORDES DE LAS IMAGENES NO SALGAN DEL CONTENEDOR */
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}